.all{min-height:100vh;min-width:100vw;background-image:url(images/background9.jpg)}.nav{max-width:100vw;height:25vh;border-left:10px ridge purple;border-bottom:10px ridge purple;display:flex;flex-direction:row;background-color:rgba(82,39,82,0.2)}.nav h1{text-align:center;padding-top:5%;font-family:'Ultra', serif;font-size:70px;text-align:center;margin:auto}.nav h1 span:hover{text-shadow:2px 2px 4px purple}.nav ul{display:flex;flex-direction:column;justify-content:space-around;margin-left:0;margin-right:3%;font-family:'Acme', sans-serif}.nav ul a{text-decoration:none}.nav ul li:hover{color:purple;cursor:pointer}
